import React from 'react';
import PropTypes from 'prop-types';
import Particles from 'react-particles-js';
import './main.css';

import {API, GETTOP} from './api';

import { Panel, PanelHeader,Search,PanelHeaderBack,Header,Snackbar,ActionSheet,PanelSpinner,MiniInfoCell,Subhead, Tabs, TabsItem, Banner, Placeholder, List, Counter, SubnavigationBar, RichCell, SubnavigationButton, Title, PanelHeaderContent, Button, Group, Cell, Div, Avatar } from '@vkontakte/vkui';
import { Icon28VideoOutline } from '@vkontakte/icons';

const Settings = ({ id, go, Info, setPopout}) => (
	<Panel id={id} style={{background: "white"}}>
	<PanelHeader>Настройки</PanelHeader>
	<Group>
	<List>
			<Cell expandable before={<Icon28VideoOutline />} after={Info.settings.anim === true ? "Включено" : "Выключено"} onClick={async () => {
        const response = await API('settings', 'type=anim')
				Info.settings.anim = response.response
				setPopout(<Snackbar
				duration={1000}
        onClose={() => setPopout(null)}>
        Анимация {response.response === true ? "включена" : "выключена"}
        </Snackbar>)
			}}>Вкл/Выкл анимацию</Cell>
			<Cell expandable before={<Icon28VideoOutline />}>Основные</Cell>
			<Cell expandable before={<Icon28VideoOutline />}>Приватность</Cell>
	</List>
	</Group>
	<div style={{marginTop: '100px'}}>
	</div>
	</Panel>
);

Settings.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Settings;
